<template>
  <div id="app-logic">
    <router-view/>
  </div>
</template>

<script>

  var sidenav;

    export default {
        name: "Reserva",
      data: function() {
          return globaldata;
      },
      methods: {
          ir: function(n) {
            sidenav.close();
            this.$router.push({name: n,});
          }
      },
      mounted: function() {
        sidenav = M.Sidenav.init(document.querySelector('.sidenav'), {});
      }
    }
</script>

